<template>
    <div class = "about">
        <h1>{{id ? '':''}}</h1>
        <el-form label-width = "120px" @submit.native.prevent = "save">
            <el-form-item label = "">
                <el-select v-model = "model.categories" multiple>
                    <el-option 
                        v-for="item in categories"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                    ></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label = "" style = "width: 40rem;">
                <el-input v-model = "model.title"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-switch v-model="model.hot"></el-switch>
            </el-form-item>
            
            <el-form-item label = "">
                <!--  vue2-editor -->
                <vue-editor usecustomimagehandler 
                @image-added="handleImageAdded" v-model="model.body"></vue-editor>
            </el-form-item>

            <el-form-item>
                <el-button type="warning" native-type = "el-submit"></el-button>  
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
//  npm install vue2-editor
//  vue2-editor
import { VueEditor } from "vue2-editor";

export default {
    //  vue2-editor
    components: {
         VueEditor
    },
    //propsid
    //this.$router.params.id
    props:{
        id:{}
    },
    data() {
        return {
            categories:[],
            model: {},
        }
    },
    methods: {
        async save() {
            // console.log('save');
            if (!this.model.categories.length) {   
                this.$message.error('')
                return
            }
            if (!this.model.title) {   
                this.$message.error('')
                return
            }
            if (!this.model.body) {
                this.$message.error('')
                return
            }
            
            let res;
            if (this.id) {
                // ${}``
                res = await this.$http.put(`rest/articles/${this.id}`, this.model);
            } else {
                res = await this.$http.post('rest/articles', this.model);
            }
            // list
            this.$router.push('/articles/list');
            this.$message({
                type:'success',
                message:''
            })
        },
        // 
        async fetch() {
            const res = await this.$http.get(`rest/articles/${this.id}`);
            this.model = res.data;
        },
        async fetchCategories() {
            const res = await this.$http.get(`rest/articles/cate`);
            this.categories = res.data;
        },
        
        async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
            const formData = new FormData();
            formData.append("file", file);

            const res = await this.$http.post('upload', formData);
            Editor.insertEmbed(cursorLocation, "image", res.data.url);
            resetUploader();
        }
    },
    created() {
        this.fetchCategories();
        this.id && this.fetch();
    }
}
</script>

